table {
  border-collapse: collapse;
  width: 73%;
  margin: auto;
}
  
table td, table th {
  border: 1px solid #ddd;
  padding: 8px;
}
  
table tr:nth-child(even){background-color: #f2f2f2;}
  
table tr:hover {background-color: #ddd;}
  
table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #04AA6D;
  color: white;
}

.delete{
  background: red;
  color: white;
  font-weight: bold;
  cursor: pointer;
  
}

.delete:hover{
  background: tomato;
  transform: translateY(-1px);
  
}

  
